.preloader {
  background-color: $white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  position: fixed;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: 100000;

  &__squre {
    position: relative;
    width: 10rem;
    height: 10rem;
    overflow: hidden;
    border: 5px solid $cryo;
  }

  &__color {
    position: absolute;
    background-color: $cryo;
    height: 0;
    width: 100%;
    left: 0;
    top: 0;
    z-index: 10;
  }

  &__md {
    margin-top: 3rem;
    color: $cryo;
    font-family: "proximaB";
    font-size: 2rem;
    text-transform: uppercase;
    letter-spacing: 0.5rem;

    span {
      display: inline-block;
    }
  }
}
